import React, {FormEventHandler, useContext, useEffect, useState} from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Grid from '@material-ui/core/Grid';
import {Icon} from '@material-ui/core';
import Typography from '@material-ui/core/Typography';
import {makeStyles} from '@material-ui/core/styles';
import {Link, useHistory} from 'react-router-dom';
import {toast} from "react-toastify";
import {HOME} from "../constants";
import {FirebaseContext} from "../firebase";
import * as yup from "yup";
import LoadingBackground from "../components/LoadingBackdrop";
import {UserStoreContext} from "../providers/UserProvider";
import Footer from "../components/Footer";
import InputPassword from "../components/forms/InputPassword";


const useStyles = makeStyles(theme => ({
    root: {
        height: '100vh',
    },
    image: {
        backgroundImage: 'url(./apply/assets/images/um-icamp-2020-background-beach.png)',
        backgroundRepeat: 'no-repeat',
        backgroundColor:
            theme.palette.type === 'dark' ? theme.palette.grey[900] : theme.palette.grey[50],
        backgroundSize: 'cover',
        backgroundPosition: 'center',
    },
    paper: {
        margin: theme.spacing(8, 4),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: '#077a80',
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

export default function SignInSide() {
    const classes = useStyles();
    const [uid] = useState<String>(localStorage.getItem('uid') as String);
    const firebaseApp = useContext(FirebaseContext);
    const userStore = useContext(UserStoreContext);
    const history = useHistory();
    const [email, setEmail] = useState<string>('');
    const [password, setPassword] = useState<string>('');
    const [loading, setLoading] = useState<boolean>(false);
    const validationSchema = yup.object({
        email: yup
            .string()
            .email(),
        password: yup
            .string()
            .min(6),
    });

    const invalidForm: boolean = !validationSchema.fields.email.isValidSync(email) || !validationSchema.fields.password.isValidSync(password);


    useEffect(() => {
        if (uid) history.push(HOME);

    }, [uid, history]);

    const onSubmitHandler: FormEventHandler = async (e: React.FormEvent<HTMLInputElement>) => {
        e.preventDefault();
        if (!invalidForm) {
            setLoading(true);

            const res = await firebaseApp?.doSignInWithEmailAndPassword(email, password);
            if (res?.hasError()) {
                toast.error(res?.error?.message);
                setLoading(false);
            } else {
                await userStore?.initialize();
                history.push(HOME);
            }

        }
    };

    return (
        <Grid container component="main" className={classes.root}>
            <LoadingBackground open={loading}/>
            <CssBaseline/>
            <Grid item xs={false} sm={4} md={7} className={classes.image}/>
            <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
                <div className={classes.paper}>
                    <div>
                        <img
                            alt={'UM iCamp 2020 - Sign In Logo'}
                            style={{maxHeight: '200px'}}
                            src={'./apply/assets/images/um-icamp-2020.png'}/>
                    </div>
                    <Avatar className={classes.avatar}>
                        <Icon>lock_outlined_icon</Icon>
                    </Avatar>
                    <Typography component="h1" variant="h5">
                        Sign in
                    </Typography>
                    <form className={classes.form} onSubmit={onSubmitHandler}>
                        <TextField
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            id="email"
                            label="Email Address"
                            name="email"
                            autoComplete="email"
                            autoFocus
                            onChange={(e) => setEmail(e.currentTarget.value)}
                        />

                        <InputPassword onChange={(e) => setPassword(e.currentTarget.value)} />

                        <Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={classes.submit}
                            disabled={invalidForm || loading}

                        >
                            Sign In
                        </Button>
                        <br/>
                        <br/>
                        <Grid container>
                            <Grid item xs>
                                <Link to="/" style={{
                                    textDecoration: 'none',
                                    color: '#000000de'
                                }}>
                                    Forgot password?
                                </Link>
                            </Grid>
                            <Grid item>
                                <Link to="/sign-up" style={{
                                    textDecoration: 'none',
                                    color: '#000000de'
                                }}>
                                    {"Don't have an account? Sign Up"}
                                </Link>
                            </Grid>
                        </Grid>
                        <Box mt={5}>
                            <Footer/>
                        </Box>
                    </form>
                </div>
            </Grid>
        </Grid>
    );
}